@media only screen and (min-width: 768px) {
  html {
    background: url("/images/BramTankink.jpg") no-repeat center fixed;
    background-size: cover;
  }
}

body {
  margin: 0;
}

div.pageContainer{
  margin-left: 0;
  margin-right: 0;
  padding-top: 7vh;
  padding-left: 5px;
  padding-right: 5px;
  background-color: rgba(222, 222, 232, 0.9);
  height: 93vh;
}
@media only screen and (min-width: 768px) {
  div.pageContainer {
    margin-left: 10vw;
    margin-right: 10vw;
  }
}
div.navbar{
    display: flex;
    margin: 0;
    background-color: #444;
    height: 5vh;
    width: 100vw;
    z-index: 999;
    position: absolute;
    box-shadow: 0px 10px 10px 0px rgba(136,136,136,0.3);
}
div.navbar ul{
    margin-left: auto;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: inline-block;
}
div.container{
    display: flex;
    margin: 0;
}
div.ridertablecontainer{
    width: 50%;
}
div.usertablecontainer{
    width: 50%;
}
textarea.queryInputBox{
    font-size: 10px;
    background: black;
    color: white;
}

div.outputTableContainer{
    height: 60vh;
    overflow: scroll;
}

table.outputTable{
    font-size: 10px;
    background: black;
    color: white;
}

div.container{
    width: 100%;
}
